<template>
  <div class="wrapper  alert" :class="'alert-' + settings.answerType">
    <h3>{{settings.message}}</h3>
    <hr>
    <button class="btn  btn-primary" @click="onContinue">Continue</button>
  </div>
</template>

<script>
  export default {
    data() {
      return {}
    },
    props: ['settings'],
    methods: {
      onContinue() {
        this.$emit('onContinue')
      }
    }
  }
</script>

<style lang="scss" scoped>
</style>



