<template>
  <div class="wrapper  alert  alert-info">
    <h3>{{x}} + {{y}} = ?</h3>
    <h3 class="timer">Timer: {{timerLocal}}</h3>
    <hr>
    <div class="buttons">
      <button class="btn  btn-success" v-for="number in answerArr" @click="onAnswer(number)">{{number}}</button>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        timerLocal: this.settings.timer,
      }
    },
    props: ['settings'],
    computed: {
      basicNumberMax() {
        return this.settings.level * this.settings.questionRange + this.settings.questionRange
      },
      x() {
        return calcRandomNumbInRange(this.settings.questionRange, this.basicNumberMax)
      },
      y() {
        return calcRandomNumbInRange(this.settings.questionRange, this.basicNumberMax)
      },
      answerRange() {
        return this.settings.level * this.settings.answerRange
      },
      btnCount() {
        return this.settings.level + this.settings.btnCount
      },
      answerNum() {
        return this.x + this.y;
      },
      answerMsgError() {
        return `${this.x} + ${this.y} = ${this.answerNum}`;
      },
      answerArr() {
        const arrAnswers = [this.answerNum];
        while (arrAnswers.length < this.btnCount) {
          let answerRandom = calcRandomNumbInRange(this.answerNum - this.answerRange, this.answerNum + this.answerRange);
          if (arrAnswers.indexOf(answerRandom) == -1) {
            arrAnswers.push(answerRandom);
          }
        }
        arrAnswers.sort(() => Math.random() - 0.5);
        return arrAnswers;
      }
    },
    methods: {
      onAnswer(num) {
        if (num === this.answerNum) {
          this.$emit('onSuccess');
        } else {
          this.$emit('onError', this.answerMsgError);
        }
      }
    },
    mounted() {
      const vmObj = this;
      let timerLocal = setTimeout(function tik() {
        if (vmObj.timerLocal > 0) {
          vmObj.timerLocal--;
          timerLocal = setTimeout(tik, 1000);
        }
      }, 1000)
    },
    watch: {
      timerLocal: function (val) {
        if (val === 0) {
          this.$emit('onTimeOver', this.answerMsgError);
        }
      }
    }
  }

  function calcRandomNumbInRange(min, max) {
    return Math.round(Math.random() * (max - min)) + min;
  }
</script>

<style lang="scss" scoped>
  .wrapper {
    position: relative;
  }

  .timer {
    position: absolute;
    top: 15px;
    right: 0;
    width: 130px;
    height: 30px;
    text-align: left;
  }
</style>



