<template>
  <div class="wrapper  alert  alert-secondary">
    <h3>Are you ready?</h3>
    <hr>
    <button class="btn  btn-success" @click="onStart">Start</button>
  </div>
</template>

<script>
  export default {
    data() {
      return {}
    },
    methods: {
      onStart() {
        this.$emit('onStart');
      }
    }
  }
</script>

<style lang="scss" scoped>
</style>



