<template>
  <div class="wrapper  alert  alert-primary">
    <h3>Success: {{attemptsSuccess}} &#8195; Error: {{attemptsError}}</h3>
    <hr>
    <button class="btn  btn-success" @click="onRepeat">Repeat level</button>
    <button class="btn  btn-success" @click="onNextLevel">Next level</button>
    <button class="btn  btn-success" @click="onStartOver">Reset level</button>
    <button class="btn  btn-success" @click="onShowAllStats">All stats</button>
    <div class="modal" v-show="popapAllStats">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Stats for all levels</h5>
            <button type="button" class="close" @click="onCloseAllStats">
              <span>&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="stats-item" v-for="(level, index) in allResult">
              <h5>Level: {{index + 1}}</h5>
              <h5>Success: {{level.success}}</h5>
              <h5>Error: {{level.error}}</h5>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" @click="onCloseAllStats">Close</button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        popapAllStats: false
      }
    },
    props: ['attemptsSuccess', 'attemptsError', 'allResult'],
    methods: {
      onRepeat() {
        this.$emit('onRepeat')
      },
      onNextLevel() {
        this.$emit('onNextLevel')
      },
      onStartOver() {
        this.$emit('onStartOver')
      },
      onCloseAllStats() {
        this.popapAllStats = false
      },
      onShowAllStats() {
        this.popapAllStats = true
      }
    }
  }
</script>

<style lang="scss" scoped>
  .modal {
    display: block;
  }

  .modal-body {
    max-height: 340px;
    overflow: auto;
  }

  .stats-item {
    display: flex;
    justify-content: space-around;
    h5 {
      min-width: 92px;
      text-align: left;
    }
  }
</style>



