import Vue from 'vue'
import App from './App.vue'
import AppStartScreen from './components/StartScreen.vue';
import AppQuestionScreen from './components/QuestionScreen.vue';
import AppAnswerScreen from './components/AnswerScreen.vue';
import AppResultScreen from './components/ResultScreen.vue';

Vue.component('AppStartScreen', AppStartScreen);
Vue.component('AppQuestionScreen', AppQuestionScreen);
Vue.component('AppAnswerScreen', AppAnswerScreen);
Vue.component('AppResultScreen', AppResultScreen);

new Vue({
  el: '#app',
  render: h => h(App)
})



