<template>
  <div class="training">
    <div class="wrapper">
      <h1>Math training</h1>
      <h2>Level: {{settings.level}}</h2>
    </div>
    <hr>
    <div class="progress">
      <div class="progress-bar" :style="progressbar"></div>
    </div>
    <div class="box">
      <transition name="rotate" mode="out-in">
        <app-start-screen v-if="state=='start'"
                @onStart="onStart">
        </app-start-screen>
        <app-question-screen v-else-if="state=='question'"
                @onSuccess="onSuccess" @onError="onError" @onTimeOver="onTimeOver"
                :settings="settings">
        </app-question-screen>
        <app-answer-screen v-else-if="state=='answer'"
                @onContinue="onContinue"
                :settings="settings">
        </app-answer-screen>
        <app-result-screen v-else-if="state=='result'"
                @onRepeat="onRepeat" @onNextLevel="onNextLevel" @onStartOver="onStartOver"
                :attemptsSuccess="attempts.success" :attemptsError="attempts.error" :allResult="allResult">
        </app-result-screen>
        <div v-else>Unknown state</div>
      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        state: 'start',
        message: {
          success: 'Good job!',
          error: 'Error! ',
          timeOver: 'Your time is over! ',
        },
        attempts: {
          success: 0,
          error: 0,
          total: 3,
        },
        settings: {
          level: 1,
          timer: 10,
          questionRange: 100,
          answerRange: 20,
          btnCount: 2,
          answerType: '',
          message: '',
        },
        allResult: [],
      }
    },
    computed: {
      attemptsAll() {
        return this.attempts.success + this.attempts.error
      },
      progressbar() {
        return {
          width: (this.attemptsAll / this.attempts.total * 100) + '%'
        }
      }
    },
    methods: {
      getAnswer() {
        this.state = 'answer';
      },
      setError() {
        this.settings.answerType = 'warning';
        this.attempts.error += 1;
      },
      onStart() {
        this.state = 'question'
      },
      onSuccess() {
        this.settings.answerType = 'success';
        this.settings.message = this.message.success;
        this.attempts.success += 1;
        this.getAnswer();
      },
      onError(msg) {
        this.settings.message = this.message.error + msg;
        this.setError();
        this.getAnswer();
      },
      onTimeOver(msg) {
        this.settings.message = this.message.timeOver + msg;
        this.setError();
        this.getAnswer();
      },
      onContinue() {
        if (this.attemptsAll < this.attempts.total) {
          this.state = 'question'
        } else {
          this.state = 'result';
          this.allResult.push(Object.assign({}, this.attempts));
        }
      },
      onDone() {
        this.state = 'start';
        this.attempts.success = 0;
        this.attempts.error = 0;
      },
      onRepeat() {
        this.onDone();
        this.allResult.pop().push(Object.assign({}, this.attempts));
      },
      onNextLevel() {
        this.onDone();
        this.settings.level++
      },
      onStartOver() {
        this.onDone();
        this.settings.level = 1;
        this.allResult.length = 0
      }
    }
  }
</script>

<style lang="scss">
  .training {
    padding: 30px 10px 50px;
    margin: 0 auto;
    min-width: 400px;
    max-width: 1000px;
    text-align: center;
    .alert {
      padding: 60px 30px;
    }
    h3 {
      margin-bottom: 30px;
    }
    .box {
      margin-top: 10px;
    }
    .btn {
      text-transform: uppercase;
      margin: 15px 20px 10px;
    }
    .rotate-enter-active {
      animation: rotateIn 0.3s linear;
    }

    .rotate-leave-active {
      animation: rotateOut 0.3s linear;
    }
  }

  @keyframes rotateIn {
    0% {
      transform: rotateX(90deg);
    }
    100% {
      transform: rotateX(0);
    }
  }

  @keyframes rotateOut {
    0% {
      transform: rotateX(0deg);
    }
    100% {
      transform: rotateX(90deg);
    }
  }
</style>


